.footer {
  padding-top: 140px;
  padding-bottom: 40px;
  background-color: $c-heading;
  @include cover;
  @include md {
    padding-top: 40px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include xs {
    padding-top: 20px;
    padding-bottom: 20px; }
  &__title {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      margin-bottom: 10px; } }
  &__bottom {
    padding-top: 45px;
    @include flex(space-between, center);
    @include lg {
      padding-top: 35px; }
    @include md {
      padding-top: 25px; }
    @include sm {
      padding-top: 20px;
      display: block; }
    @include xs {
      padding-top: 15px; } }
  &__copy {
    a {
      text-decoration: underline; }
    @include sm; }

  &__contacts {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      margin-bottom: 10px; } }
  &__nav {
    @include lg {
      margin-bottom: 20px; } }
  &__logo {
    min-width: 280px;
    padding-right: 20px;
    @include sm {
      min-width: 0;
      display: inline-block; } } }

