.header {
  $this: &;
  position: relative;
  z-index: 2;
  &.is-fixed {
    position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
    top: 0;
    background-color: $c-heading;
    @include sm;
    #{$this}__top {
      @extend .container;
      border-bottom: 0;
      height: 118px;
      // padding-top: 15px
      // padding-bottom: 15px
      @include lg {
        position: static; }
      @include sm {
        padding-bottom: 5px;
        padding-top: 5px;
        height: 80px; }
      @include xs {
        height: 60px; } }
    #{$this}__bottom {
      display: none; }
    #{$this}__nav {
      display: none; }
    #{$this}__contacts {
      @include flex();
      @include lg {
        display: none; } }
    #{$this}__burger {
      display: none; }
    #{$this}__burger-contacts {
      @include lg {
        @include flex(center, center, column); } } }
  &__contacts {
    display: none;
    @include lg {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: $c-heading;
      padding-left: 15px;
      padding-right: 15px; } }
  &__logo {
    @include flex(flex-start, center);
    padding-top: 10px;
    padding-bottom: 10px;
    @include lg {
      flex-grow: 1; } }
  &__top {
    border-bottom: 1px solid  #b5d0ff;
    @include flex(space-between);
    position: relative; }
  &__nav {
    flex-grow: 1;
    height: auto;
    text-align: right;
    padding-right: 20px;
    @include flex(flex-end, center);
    @include lg {
      display: none;
      position: absolute;
      top: 100%;
      width: 100%;
      left: 0;
      z-index: 10;
      text-align: center;
      background-color: $c-heading;
      padding-right: 0;
      padding-top: 15px;
      padding-bottom: 15px; } }
  &__btn {
    @include flex(flex-start, center);
    @include lg {
      margin-right: 10px; }
    @include sm {
      display: none; } }
  &__bottom {
    padding-top: 20px; }
  &__burger {
    display: none;
    @include lg {
      @include flex(center, center, column); } }
  &__burger-contacts {
    display: none; } }


.phones {
  @include flex(space-between, center, row, wrap);
  @include md {
    justify-content: flex-start; } }

.phone {
  @include flex(flex-start, center);
  color: $white;
  @include bold;
  @include tr(color .3s);
  @include hover {
    color: $accent; }
  @include md {
    width: 33.33333%; }
  @include sm {
    width: 50%; }
  .icon {
    margin-right: 5px;
    font-size: .8em; }
  &--lg {
    font-size: $fz-lg;
    @include sm {
      font-size: 21px; }
    @include xs {
      justify-content: center; } }
  &--sm {
    .phone__icon {
      display: inline-block;
      width: 45px;
      margin-right: 5px;
      text-align: center;
      .icon {
        margin: 0; } } }
  &--xs {
    @extend .phone--sm;
    .phone__icon {
      margin-right: 0; } }
  &--blue {
    color: $c-heading; } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  // margin-top: 14px

  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px; }

  span:after {
    top: 18px; }

  span {
    position: relative;
    display: block; }

  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: $white;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }


  &.is-active span {
    background-color: transparent; }

  &.is-active span:before {
    transform: rotate(45deg) translate(-1px, 0px); }

  &.is-active span:after {
    transform: rotate(-45deg) translate(6px, -7px); }

  &--contacts {
    border-radius: 50%;
    background-color: $accent;
    @include flex(center, center);
    width: 50px;
    height: 50px;
    color: $c-heading; } }

