body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  @include reg;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-md; } }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit;
    color: inherit; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.overflow-hidden {
  overflow: hidden; }

.chat-btns {
  position: fixed;
  top: 50%;
  transform: translate(0, -50%);
  right: 0;
  z-index: 10;
  &__btn {
    margin-bottom: 10px;
    @include xxl {
      .btn--soc {
        font-size: 0;
        min-width: 0;
        padding: 0;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        .icon {
          font-size: $fz;
          margin: 0; } } } } }

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #cef2fe;
  &__img {
    margin-right: 20px;
    @include xs {
      margin-right: 0;
      img {
        max-width: 80%; } } }
  &__subttl {
    display: block; }
  &__title {
    color: $white;
    display: block;
    font-size: $fz-lg;
    @include xs {
      font-size: $fz; }
    em {
      color: $accent; } } }

.btn {
  display: inline-block;
  background-color: $accent;
  border: 1px solid  $accent;
  font-size: $fz;
  text-align: center;
  color: #5d5d59;
  @include bold;
  padding: 13px 20px;
  min-width: 230px;
  border-radius: 30px;
  @include tr(color .3s, background-color .3s, border-color .3s);
  @include hover {
    background-color: transparent;
    color: $accent; }
  @include sm {
    padding: 8px 15px; }
  @include xs {
    min-width: 0;
    width: 100%; }

  &--transp-white {
    color: $white;
    border-color: $white;
    background-color: transparent;
    @include hover {
      background-color: $white;
      border-color: $white;
      color: #5d5d59; } }
  &--transp-text {
    color: $c-text;
    border-color: $c-text;
    background-color: transparent;
    @include hover {
      background-color: $c-text;
      border-color: $c-text;
      color: #5d5d59; } }
  &--transp-heading {
    color: $c-heading;
    border-color: $c-heading;
    background-color: transparent;
    @include hover {
      background-color: $c-heading;
      border-color: $c-heading;
      color: #5d5d59; } }
  &--viber {
    background-color: #8a51a2;
    border-color: #8a51a2; }
  &--whatsapp {
    background-color: #40d251;
    border-color: #40d251; }
  &--soc {
    color: $white;
    @include inline-flex(center, center);
    @include hover {
      background-color: $accent;
      color: #5d5d59;
      border-color: $accent; }
    .icon {
      margin-right: 5px; } } }

table {
  @include sm {
    width: 720px; }
  tr {
    background-color: $white;
    &:nth-child(even) {
      background-color: #f6f8ff; }
    &:first-child {
      td {
        padding-top: 40px;
        padding-bottom: 40px;
        @include md {
          padding-top: 30px;
          padding-bottom: 30px; } } }
    &:last-child {
      background-color: $c-heading;
      td {
        padding-top: 30px;
        padding-bottom: 30px;
        @include md {
          padding-top: 20px;
          padding-bottom: 20px; } } } }
  td {
    padding-top: 10px;
    padding-bottom: 10px; } }

