.hero {
  @include cover;
  // min-height: 100vh
  position: relative;
  // margin-bottom: 50px
  &__img {
    position: absolute;
    @include cover;
    background-size: contain;
    z-index: 0;
    right: 0;
    bottom: -50px;
    width: percentage(700/1920);
    height: percentage(325/885);
    @include md {
      display: none; } }
  &__block {
    position: relative;
    z-index: 1;
    padding-top: 12%;
    padding-bottom: 12%; }
  &__title {
    margin-bottom: 35px;
    h1, .title {
      margin-bottom: 10px; }
    @include md {
      margin-bottom: 15px; } }
  &__left {
    padding-top: 35px;
    @include md {
      margin-bottom: 15px; } }
  &__meta {} }



.meta {
 @include flex(flex-start, center, row, wrap); }

.meta-item {
  @include flex(flex-start, center);
  color: $white;
  width: 50%;
  margin-bottom: 30px;
  @include xs {
    display: block;
    text-align: center; }

  &__icon {
    margin-right: 14px;
    background-color: rgba(#adb9d1, .4);
    border-radius: 50%;
    height: 130px;
    width: 130px;
    padding: 24px;
    @include flex(center, center);
    border: 12px solid rgba(#6c87b7, .4);
    position: relative;
    @include before {
      border: 12px solid rgba(#8998b7, .4);
      border-radius: 50%;
      @include coverdiv; }
    @include lg {
      width: 100px;
      height: 100px;
      margin-right: 5px;
      .icon {
        font-size: 25px; } }
    @include md {
      width: 80px;
      height: 80px;
      border-width: 8px;
      @include before {
        border-width: 8px; } }
    @include xs {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10px; }
    .icon {
      font-size: 42px; } } }
