.section {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: $white;
  @include md {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include xs {
    padding-top: 20px;
    padding-bottom: 20px; }
  &__title {
    text-align: center;
    margin-bottom: 80px;
    .title, h2 {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 60px; }
    @include sm {
      margin-bottom: 40px; }
    @include xs {
      margin-bottom: 20px; } }
  &__center-block {
    height: 100%;
    @include flex(center, normal, column);
    @include md {
      margin-bottom: 20px; }
    @include xs {
      text-align: center; }
    .title, h2 {
      margin-bottom: 15px;
      @include sm {
        margin-bottom: 10px; } }
    .text {
      margin-bottom: 60px;
      @include lg {
        margin-bottom: 40px; }
      @include md {
        margin-bottom: 30px; }
      @include sm {
        margin-bottom: 20px; } } }

  &--routes {
    @include cover;
    background-position: 0 100%;
    padding-top: 140px; }
  &--bg {
    @include cover; }
  &--lg {
    padding-top: 140px;
    padding-bottom: 140px; } }
