.nav {
  color: $white;
  height: 100%;
  ul {
    height: 100%; }
  li {
    display: inline-block;
    height: 100%;
    @include notlast {
      margin-right: 45px; }
    @include lg {
      display: block;
      @include notlast {
        margin-right: 0;
        margin-bottom: 10px; } } }
  a {
    display: flex;
    align-items: center;
    height: 100%;
    @include tr(color .3s);
    &.is-active {
      color: $accent;
      border-bottom: 2px solid  $accent; }
    @include hover {
      color: $accent;
      border-bottom: 2px solid  $accent; }
    @include lg {
      display: inline; } }

  &--footer {
    color: $c-text;
    li {
      display: block;
      height: auto;
      @include notlast {
        margin-bottom: 20px;
        margin-right: 0; } }
    a {
      display: inline;
      padding: 0;
      @include hover {
        border-bottom: 0;
        color: $c-text-hover; } } } }
