.contacts {
  $this: &;
  @include flex();
  @include xs {
    display: block; }
  &__list {
    width: 50%;
    @include xs {
      width: auto; } }
  &__item {
    @include notlast {
      margin-bottom: 20px;
      @include md {
        margin-bottom: 15px; }
      @include sm {
        margin-bottom: 5px; } }
    .phone {
      @include md {
        width: auto; } } }

  &--sm {
    #{$this}__list {
      width: auto; } }
  &--header {
    color: $white;
    @include flex(space-between, center);
    #{$this}__title {
      margin-right: 30px;
      @include xs {
        display: none; } }
    #{$this}__item {
      @include notlast {
        margin-bottom: 5px; } }
    #{$this}__list {
      @include notlast {
        margin-right: 30px;
        @include xs {
          margin-right: 0; } } } } }
